export default {
	data() {
		return {
			list: [],
			show: false,
			request_no: '',
			id: ''
		}
	},
	methods: {
		// 查询还款申请
		link(id){
			uni.navigateTo({
				url: `/pages/index/card-keeper/plan-list?cardid=${id}&plantype=1`
			})
		},
		// 获取信用卡列表
		getData(){
			this.$store.dispatch('getQuickCardList',{
				plantype: 1
			}).then(res=>{
				this.list = res.data
			})
		},
		// 激活或者还款前的验证
		addPlan(id){
			this.id = id
			this.$store.dispatch('getQuickAuth',{
				cardid: id,
				token: uni.getStorageSync('token')
			}).then(res=>{
				if(res.isSMS==1){
					this.show = true
					this.request_no = res.request_no
				}else{
					uni.showModal({
						title: '请您完成银联认证',
						success: (d)=>{
							if(d.confirm){
								let data = {
									request_no: this.request_no,
									token: uni.getStorageSync('token')
								}
								this.$store.dispatch('setCreditCardTwo',data).then(res=>{
									uni.navigateTo({
										url:`/pages/index/card-keeper/booking-repayment?id=${this.id}`
									})
								}).catch(err=>{})
							}else{
								uni.showToast({
									title: '您取消了绑卡！',
									icon: 'none'
								})
							}
						}
					})
				}
			}).catch(err=>{
				if(err.code==2){
					uni.navigateTo({
						url:`/pages/index/card-keeper/booking-repayment?id=${this.id}`
					})
				}else if(err.code == 3){
					uni.navigateTo({
						url:`/pages/index/card-keeper/plan-list?cardid=${this.id}&plantype=1`
					})
				}else if (res.code == 4){
					uni.showToast({
						title: '暂不支持此银行',
						icon: 'none'
					});
				}
			})
		},
		// 校验验证码
		confirm(){
			if(!this.code){
				uni.showToast({
					title: '验证码不能为空',
					icon: 'none'
				})
				return
			}
			let data = {
				request_no: this.request_no,
				smsCode: this.code,
				token: uni.getStorageSync('token')
			}
			this.$store.dispatch('setCreditCardTwo',data).then(res=>{
				this.show = false
				uni.navigateTo({
					url:`/pages/index/card-keeper/booking-repayment?id=${this.id}`
				})
			}).catch(err=>{})
		},
		// 修改账单日
		eidtDay(id){
			uni.showModal({
				title: '提示',
				content: '确认修改账单日期么？',
				success: (res)=>{
					if(res.confirm){
						uni.navigateTo({
							url:`/pages/mine/bank-card/edit-credit-card?id=${id}`
						})
					}
				}
			})
		},
		addCard(){
			uni.navigateTo({
				url: '/pages/mine/bank-card/add-credit-card?type=quick'
			})
		}
	},
	onShow(){
		this.getData()
	},
	onNavigationBarButtonTap(){
		uni.navigateTo({
			url: `/pages/mine/sys-message/detail?id=17`
		})
	}
}
